.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

.usertable{
	margin-right: 20px;
	text-align: left;
}

.usertable tr{
	border-bottom: 1px solid #000;
}

.single-user,
.user-post-list{
	text-align: left;
}

.todo-list{
	margin-top: 30px;
}

.todo-list ul li{
	list-style: none;
	margin: 10px;
}

.nav-link{
	display: flex;
	justify-content: center;
}

.nav-link li{
	list-style:none;
	padding: 5px;
}


.selected{
	color: darkred;
}

.nav-link .link{
	text-decoration: none;
	color: #000;
}

.nav-link .active{
	color:cornflowerblue;
	;
}


.Canvas-main{
	display:flex;
	justify-content: space-evenly;
}

@media only screen and (max-width: 768px) {
	.Canvas-main{
		display:block;
	}
}

.Canvas-main .canvas-manage{
	margin-bottom:30px;
	text-align:left
}


.Canvas-main .canvas-manage div{
	margin-top: 20px;
	display: flex;
	margin-bottom: 20px;
}

.Canvas-main .canvas-manage div p{
padding-right: 10px;
padding-left: 10px;
}

.canvas-manage .image-main{
	display: none;
}

.mems-text-list{
	padding-top: 30px;
}
